/* --- Unified chat styles for client + admin --- */

.support-chat-layout {
  display: flex;
  gap: 24px;
  flex-wrap: wrap;
  width: 100%;
  box-sizing: border-box;
}

.chat-list-sidebar {
  flex: 0 0 320px;
  min-width: 0;
  border: 1px solid var(--glass-border);
  border-radius: 24px;
  background: var(--surface-muted);
  box-shadow: var(--shadow);
  display: flex;
  flex-direction: column;
  backdrop-filter: var(--glass-blur);
  overflow: hidden;
}
.chat-list-header {
  padding: 20px;
  border-bottom: 1px solid var(--glass-border);
}
.chat-list-header h2 {
  margin: 0;
}
.chat-list-items {
  overflow-y: auto;
  flex-grow: 1;
  -webkit-overflow-scrolling: touch; /* –ü–ª–∞–≤–Ω–∞—è –ø—Ä–æ–∫—Ä—É—Ç–∫–∞ –Ω–∞ iOS */
}

.chat-list-item {
  padding: 16px 20px;
  border-bottom: 1px solid var(--glass-border);
  cursor: pointer;
  transition: background-color 0.2s, color 0.2s;
}
.chat-list-item:hover {
  background-color: rgba(255,255,255,0.08);
}
.chat-list-item.active {
  background-color: var(--surface-strong);
  color: var(--text-primary);
  border-left: 3px solid var(--accent-primary);
}
.chat-list-item.closed {
  opacity: 0.6;
}

.item-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
  gap: 12px;
}
.item-email {
  font-weight: 600;
  color: var(--text-primary);
}
.item-date {
  font-size: 0.8em;
  color: var(--text-secondary);
}
.item-subject {
  font-size: 0.9em;
  color: var(--text-secondary);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.status-badge-small {
  padding: 2px 6px;
  border-radius: 999px;
  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
  margin-top: 8px;
  display: inline-block;
}
.status-badge-small.status-OPEN { background-color: var(--accent-green); }
.status-badge-small.status-CLOSED { background-color: #6c757d; }

.chat-window-wrapper {
  flex: 1 1 420px;
  min-width: 0;
  display: flex;
}
.chat-window {
  display: flex;
  flex-direction: column;
  width: 100%;
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: 24px;
  box-shadow: var(--shadow);
}
.chat-window.empty,
.chat-window.loading,
.chat-window.error {
  align-items: center;
  justify-content: center;
  color: var(--text-secondary);
  padding: 24px;
}
.chat-window.empty section {
  width: 100%;
  max-width: 500px;
}

.chat-header {
  padding: 18px 24px;
  border-bottom: 1px solid var(--glass-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--glass-bg);
  border-radius: 24px 24px 0 0;
}
.chat-user {
  font-size: 0.9em;
  color: var(--text-secondary);
}

.chat-messages {
  flex-grow: 1;
  overflow-y: auto;
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  -webkit-overflow-scrolling: touch; /* –ü–ª–∞–≤–Ω–∞—è –ø—Ä–æ–∫—Ä—É—Ç–∫–∞ –Ω–∞ iOS */
}
.chat-bubble {
  padding: 12px 16px;
  border-radius: 16px;
  max-width: 70%;
  line-height: 1.5;
  border: 1px solid transparent;
}
.chat-bubble.them {
  background-color: var(--surface-muted);
  border-color: var(--glass-border);
  align-self: flex-start;
}
.chat-bubble.me {
  background-color: var(--accent-primary);
  color: #fff;
  border-bottom-right-radius: 4px;
  align-self: flex-end;
}
.bubble-sender {
  font-size: 0.8em;
  font-weight: 700;
  color: var(--text-secondary);
  margin-bottom: 4px;
}
.chat-bubble.me .bubble-sender {
  color: rgba(255,255,255,0.8);
}
.bubble-message {
  white-space: pre-wrap;
}
.bubble-time {
  font-size: 0.75em;
  color: var(--text-secondary);
  text-align: right;
  margin-top: 5px;
}
.chat-bubble.me .bubble-time {
  color: rgba(255,255,255,0.8);
}

.chat-input-form {
  display: flex;
  padding: 18px;
  border-top: 1px solid var(--glass-border);
  background: var(--glass-bg);
  border-radius: 0 0 24px 24px;
  gap: 12px;
}
.chat-input-form input {
  flex-grow: 1;
  padding: 12px 18px;
  background: var(--surface-muted);
  border: 1px solid var(--glass-border);
  color: var(--text-primary);
  border-radius: 30px;
  font-size: 16px;
  -webkit-appearance: none; /* –£–±–∏—Ä–∞–µ–º —Å—Ç–∏–ª–∏ iOS */
  appearance: none;
  box-sizing: border-box;
}

body.light .chat-input-form input {
  background: rgba(255, 255, 255, 0.8);
  border-color: rgba(0, 0, 0, 0.1);
  color: #0f172a;
}

body.dark .chat-input-form input {
  background: rgba(15, 23, 42, 0.4);
  border-color: rgba(255, 255, 255, 0.1);
  color: #e2e8f0;
}

.chat-input-form input:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 3px rgba(63, 105, 255, 0.1);
}

body.light .chat-input-form input:focus {
  background: rgba(255, 255, 255, 0.95);
}

body.dark .chat-input-form input:focus {
  background: rgba(15, 23, 42, 0.6);
}
.btn-send {
  background: var(--accent-primary);
  color: #fff;
  border: none;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  font-size: 1.4em;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.2s;
}
.btn-send:hover { background-color: var(--accent-primary-hover); }
.btn-send:disabled { opacity: 0.5; cursor: not-allowed; }

/* –ú–æ–±–∏–ª—å–Ω—ã–µ —Å—Ç–∏–ª–∏ –¥–ª—è iPhone –∏ Android */
@media (max-width: 1100px) {
  .support-chat-layout {
    flex-direction: column;
    gap: 16px;
  }
  .chat-list-sidebar {
    flex: 1 1 auto;
    min-width: 0;
    width: 100%;
  }
  .chat-window-wrapper {
    width: 100%;
    min-width: 0;
  }
}

/* –ú–æ–±–∏–ª—å–Ω—ã–µ —Å—Ç–∏–ª–∏ –¥–ª—è –º–∞–ª–µ–Ω—å–∫–∏—Ö —ç–∫—Ä–∞–Ω–æ–≤ (iPhone, Android) */
@media (max-width: 768px) {
  .support-chat-layout {
    gap: 12px;
    padding: 0;
  }

  .chat-list-sidebar {
    border-radius: 16px;
    max-height: 40vh;
  }

  .chat-list-header {
    padding: 16px;
  }

  .chat-list-header h2 {
    font-size: 1.1rem;
  }

  .chat-list-item {
    padding: 12px 16px;
  }

  .item-header {
    flex-wrap: wrap;
    gap: 8px;
  }

  .item-email {
    font-size: 0.85rem;
  }

  .item-date {
    font-size: 0.75rem;
  }

  .item-subject {
    font-size: 0.85rem;
    white-space: normal;
    overflow: visible;
    text-overflow: clip;
    max-height: 2.4em;
    line-height: 1.2;
  }

  .chat-window {
    border-radius: 16px;
    min-height: 50vh;
  }

  .chat-header {
    padding: 14px 16px;
    border-radius: 16px 16px 0 0;
  }

  .chat-header h3 {
    font-size: 1rem;
    margin: 0;
  }

  .chat-messages {
    padding: 16px;
    gap: 12px;
  }

  .chat-bubble {
    max-width: 85%;
    padding: 10px 14px;
    font-size: 14px;
  }

  .bubble-sender {
    font-size: 0.75rem;
  }

  .bubble-time {
    font-size: 0.7rem;
  }

  .chat-input-form {
    padding: 12px;
    gap: 8px;
    flex-wrap: nowrap;
  }

  .chat-input-form input {
    padding: 10px 14px;
    font-size: 16px; /* –ü—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–∞–µ—Ç –∑—É–º –Ω–∞ iOS */
    border-radius: 24px;
    min-width: 0;
    flex: 1;
  }

  .btn-send {
    width: 44px;
    height: 44px;
    font-size: 1.2em;
    flex-shrink: 0;
  }

  .chat-window.empty section {
    padding: 16px;
  }

  .support-form {
    padding: 16px;
    width: 100%;
    box-sizing: border-box;
  }

  .support-form .form-group {
    margin-bottom: 16px;
  }

  .support-form .form-group label {
    font-size: 14px;
    margin-bottom: 8px;
    display: block;
    color: var(--text-secondary);
  }

  .support-form input,
  .support-form textarea {
    font-size: 16px; /* –ü—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–∞–µ—Ç –∑—É–º –Ω–∞ iOS */
    padding: 12px;
    width: 100%;
    box-sizing: border-box;
    -webkit-appearance: none;
    appearance: none;
    border-radius: 8px;
    background: var(--bg-primary);
    border: 1px solid var(--border-color);
    color: var(--text-primary);
  }

  body.light .support-form input,
  body.light .support-form textarea {
    background: #ffffff;
    border-color: rgba(0, 0, 0, 0.15);
    color: #0f172a;
  }

  body.dark .support-form input,
  body.dark .support-form textarea {
    background: rgba(15, 23, 42, 0.5);
    border-color: rgba(255, 255, 255, 0.1);
    color: #e2e8f0;
  }

  .support-form input:focus,
  .support-form textarea:focus {
    outline: none;
    border-color: var(--accent-primary);
    box-shadow: 0 0 0 3px rgba(63, 105, 255, 0.1);
  }

  .support-form textarea {
    min-height: 100px;
    resize: vertical;
    line-height: 1.5;
  }

  .support-form button {
    width: 100%;
    max-width: none;
    padding: 12px;
    font-size: 16px;
  }
}

/* –û—á–µ–Ω—å –º–∞–ª–µ–Ω—å–∫–∏–µ —ç–∫—Ä–∞–Ω—ã */
@media (max-width: 480px) {
  .chat-list-sidebar {
    max-height: 35vh;
  }

  .chat-window {
    min-height: 60vh;
  }

  .chat-bubble {
    max-width: 90%;
    padding: 8px 12px;
  }

  .chat-input-form {
    padding: 10px;
  }

  .chat-input-form input {
    padding: 8px 12px;
  }

  .btn-send {
    width: 40px;
    height: 40px;
  }
}

/* –ò—Å–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –¥–ª—è iOS Safari */
@supports (-webkit-touch-callout: none) {
  .chat-input-form input {
    font-size: 16px !important; /* –ü—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–∞–µ—Ç –∑—É–º –ø—Ä–∏ —Ñ–æ–∫—É—Å–µ –Ω–∞ iOS */
  }

  .support-form input,
  .support-form textarea {
    font-size: 16px !important; /* –ü—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–∞–µ—Ç –∑—É–º –ø—Ä–∏ —Ñ–æ–∫—É—Å–µ –Ω–∞ iOS */
  }
}